@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya&family=Open+Sans&family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body
{
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
}

:root {
  --background: #222629;
  --primary: #222629;
  --tertiary: #222629;
}

body {
   background-color: #222629;
   background-color: var(--background);
}

#root {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

#app {
  background-color: #222629;
  background-color: var(--background);
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

#title {
  text-align: center;
  color: #FFFFFF;
  font-family: 'Playfair Display', serif;
  font-size: 50px;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

#intro-section {
  background-color: #222629;
  background-color: var(--primary);
  padding: 10px;
  height: 300px;
}

#project-section {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: auto;
  left: 0;
  background-color: #222629;
  background-color: var(--tertiary);
  border: 0;
  padding: 50px 30px 0px 30px;
  margin: 0;
  overflow-x: hidden;
}

#projects-title {
  text-align: center;
  color: #FFFFFF;
  font-family: 'Playfair Display', serif;
  font-size: 30px;
  padding-top: 20px;
  margin-bottom: 30px;
}

#grid-container {
  background-color: inherit;
}

.project-container-title {
  color: white;
  font-family: 'Alegreya', serif;
  text-align: center;
}

#contact-segment-container {
  margin-bottom: 10px;
}

#about-me-title {
  text-align: center;
  color: #FFFFFF;
  font-family: 'Playfair Display', serif;
  font-size: 30px;
  margin-bottom: 15px;
}

#about-description {
  text-align: center;
  color: #FFFFFF;
  font-size: 20px;
}

#languages-title {
  text-align: center;
  color: white;
  font-family: 'Playfair Display', serif;
  font-size: 24px;
  margin-bottom: 40px;
  margin-top: 60px;
}

#footer-div {
  padding-top: 30px;
}
